import { PriceAreaT } from '../Select/Select.conf';
import './Price.scss';

export const Price: React.FC<PriceAreaT> = ({
  register,
  name,
  col,
  setValue,
}) => {
  const handler = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;

    if (name == 'price') {
      +value.replace(/[^0-9.]*/gi, '') < 200 &&
        setValue(name, value.replace(/[^0-9.]*/gi, ''));
    } else {
      setValue(name, value.replace(/[^0-9.]*/gi, ''));
    }
  };

  return (
    <div className='pricedevice'>
      <div className='pricedevice__block'>
        <div className='titleproductadmin  '>
          <h3>
            {name
              .split('')
              .map((item, index) => {
                return index == 0 ? item.toUpperCase() : item;
              })
              .join('')}
          </h3>
        </div>
        <label>
          <div className='pricedevice__body'>
            {name == 'price' && <p className='price'>$</p>}
            <input
              type='text'
              {...register(name, {
                required: true,
                onChange: handler,
              })}
              maxLength={col}
            />
            {name == 'sale' && <p className='sale'>%</p>}
          </div>
        </label>
      </div>
    </div>
  );
};
