import { FieldValues, SubmitHandler, useForm } from 'react-hook-form';
import {
  updatepassUserHTTP,
  useAppDispatch,
  useAppSelector,
} from '../../../../store';
import './ChangePass.scss';
import {
  BtnLoading,
  InputPassword,
  Loading,
  validateUserValue,
} from '../../../../components';
import { useEffect } from 'react';

export const ChangePass: React.FC = () => {
  const { user, isLoading, error } = useAppSelector((state) => state.user);
  const dispatch = useAppDispatch();

  const {
    register,
    handleSubmit,
    formState: { errors },
    getValues,
    setValue,
    setError,
  } = useForm<FieldValues>({ mode: 'onSubmit' });

  const fetching: SubmitHandler<FieldValues> = (data) => {
    if ('id' in user && user.role != 'Admin') {
      dispatch(updatepassUserHTTP({ body: data }));
    }
  };

  useEffect(() => {
    if (!isLoading && 'message' in error) {
      validateUserValue({ data: error, setError });
    }
  }, [isLoading]);

  return (
    <div className='changepass'>
      <div className='changepass__wrap'>
        <form onSubmit={handleSubmit(fetching)}>
          <div className='changepass__input'>
            <InputPassword
              req={true}
              title='Old password'
              name='passwordold'
              register={register}
              type='password'
              textFiled='Your old password'
              setValue={setValue}
              errors={errors}
            />

            <InputPassword
              req={true}
              title='New Password'
              name='password'
              register={register}
              type='password'
              textFiled='Your new password'
              setValue={setValue}
              errors={errors}
              getValues={getValues}
            />
            <InputPassword
              req={true}
              name='passTwo'
              title='Password Repeat'
              register={register}
              type='password'
              textFiled='Repeat password'
              setValue={setValue}
              errors={errors}
              getValues={getValues}
              twopass={true}
            />
          </div>
          <div className='changepass__footer'>
            <div className='changepass__footer'>
              <button
                type='submit'
                className={
                  'id' in user && user.role == 'Admin'
                    ? 'admin changepass__btn'
                    : ' changepass__btn'
                }
              >
                {isLoading ? (
                  <BtnLoading>
                    <Loading />
                  </BtnLoading>
                ) : (
                  <p className='mini'>UPDATE</p>
                )}
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};
