import { useState, useEffect } from 'react';
import { FieldValues, SubmitHandler, useForm } from 'react-hook-form';
import { ReactComponent as Pen } from '../../../../img/icons/pen.svg';
import {
  UserRegistrationT,
  updateUserHTTP,
  useAppDispatch,
  useAppSelector,
} from '../../../../store';
import { AllInpurt } from '../../../../components/UI/Input/OtherInput';
import { BtnLoading, Loading } from '../../../../components';

import './UserBlock.scss';

export const UserBlock: React.FC = () => {
  const { user } = useAppSelector((state) => state.user);
  const obj = user as UserRegistrationT;

  const arr: UserPropertyTypes[] = [
    { name: 'FirstName', value: obj?.firstName, key: 'firstName' },
    { name: 'LastName', value: obj?.lastName, key: 'lastName' },
    { name: 'Login', value: obj?.login, key: 'login' },
    { name: 'Email', value: obj?.email, key: 'email' },
    { name: 'Number', value: obj?.number, key: 'number' },
  ];
  return (
    <div className='userblock'>
      <div className='userblock__wrap'>
        <div className='userblock__row'>
          <>
            <div className='userblock__title'>
              <p className='big'>{'id' in user && user.firstName}</p>
            </div>
            {arr.map((item: UserPropertyTypes) => {
              return <CustomerProperty key={item.name} item={item} />;
            })}
          </>
        </div>
      </div>
    </div>
  );
};

export default function CustomerProperty({ item }) {
  const [reload, setReload] = useState(false);
  const { user, isLoading } = useAppSelector((state) => state.user);
  const dispatch = useAppDispatch();

  async function fetching(data) {
    dispatch(updateUserHTTP({ ...data }));
  }

  useEffect(() => {
    if (!isLoading) {
      setReload(false);
    }
  }, [isLoading]);

  const {
    register,
    formState: { errors },
    handleSubmit,
    setValue,
  } = useForm({ mode: 'onSubmit' });

  const onSubmit: SubmitHandler<FieldValues> = (data) => {
    fetching(data);
  };

  return (
    <>
      {reload ? (
        <form onSubmit={handleSubmit(onSubmit)} className='userblock__form'>
          <div className='setsale__input'>
            <AllInpurt
              register={register}
              type='text'
              setValue={setValue}
              errors={errors}
              name={item.key}
              textFiled={item.name}
            />
          </div>
          <div className='userblock__footer'>
            <button type='submit' className='userblock__btn'>
              {isLoading ? (
                <BtnLoading>
                  <Loading />
                </BtnLoading>
              ) : (
                <p className='mini'>UPDATE</p>
              )}
            </button>
            <div className='userblock__cancel' onClick={() => setReload(false)}>
              <p className='mini'>Cancel</p>
            </div>
          </div>
        </form>
      ) : (
        <div className='userblock__items'>
          <div className='userblock__key'>
            <p>{item.name}:</p>
          </div>
          <div className='userblock__item'>
            <div className='userblock__value'>
              <p>{item.value}</p>
            </div>
          </div>
          <div
            onClick={() =>
              'id' in user && user.role == 'Admin' ? undefined : setReload(true)
            }
            className={
              'id' in user && user.role == 'Admin'
                ? 'admin userblock__icon'
                : ' userblock__icon'
            }
          >
            <Pen />
          </div>
        </div>
      )}
    </>
  );
}

export interface UserPropertyTypes {
  name: string;
  value: string;
  key: string;
}
