import { useEffect, useState } from 'react';
import './GoodsList.scss';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { FieldValues } from 'react-hook-form';
import { ReactComponent as Pen } from '../../../../img/icons/pen.svg';
import { ReactComponent as Basket } from '../../../../img/icons/trash.svg';
import { Link } from 'react-router-dom';
import { PaginationGoods } from './PaginationGoods';
import { links } from '../../../../Router';
import { LoadingWin } from '../../../../components';
import { useAppDispatch, useAppSelector } from '../../../../store';
import { favoriteAll } from '../../../../store/goodsSlices/favoriteSlice';
import { basketAll } from '../../../../store/goodsSlices/basketSlice';
import {
  deleteDeviceHTTP,
  getListGoods,
} from '../../../../store/goodsSlices/deviceListSlice/deviceListAPI';

export const GoodsList: React.FC = () => {
  const [type, setType] = useState('');
  const [page, setPage] = useState('1');
  const [searchParams, setSearchParams] = useSearchParams();

  const dispatch = useAppDispatch();
  const { goodsList, isLoadingList } = useAppSelector(
    (state) => state.deviceSelect
  );

  async function fetching() {
    dispatch(getListGoods({ typeId: type, page }));
  }

  useEffect(() => {
    document.title = `Admin`;
  }, []);

  useEffect(() => {
    setPage(searchParams.get('page') || '1');
    if (!searchParams.get('page')) {
      searchParams.set('page', '1');
      setSearchParams(searchParams);
    }
  }, []);

  useEffect(() => {
    fetching();
  }, [type, page]);

  return (
    <div className='goodslist'>
      <div className='goodslist__wrap'>
        <div className='goodslist__turn'>
          <div className='goodslist__type'>
            <p className=''>Type</p>
          </div>
          <div className='goodslist__type'>
            <p className=''>Brand</p>
          </div>
          <div className='goodslist__type'>
            <p className=''>Title</p>
          </div>
          <div className='goodslist__type'>
            <p className=''>Price</p>
          </div>
        </div>

        <LoadingWin isLoading={isLoadingList}>
          <div className='goodslist__items'>
            {goodsList?.rows?.map((item, index) => {
              return (
                <GoodsItem
                  key={item.id}
                  item={item}
                  index={index}
                  fetching={fetching}
                  page={page}
                />
              );
            })}
          </div>

          {!!goodsList?.count && (
            <PaginationGoods
              count={goodsList.count}
              setPage={setPage}
              pageOld={page}
            />
          )}
        </LoadingWin>
      </div>
    </div>
  );
};

export const GoodsItem: React.FC<GoodsType> = ({
  item,
  index,
  fetching,
  page,
}) => {
  const dispatch = useAppDispatch();
  const deleteItem = async () => {
    await dispatch(deleteDeviceHTTP({ id: +item.id }));
    await dispatch(favoriteAll());
    await dispatch(basketAll());

    fetching();
  };

  const router = useNavigate();
  return (
    <div
      className={item.change ? ' goodsitem' : 'notchange goodsitem'}
      onClick={() =>
        item?.change && document.documentElement.clientWidth < 765
          ? router('/admin/item/' + item.id)
          : undefined
      }
    >
      <div className='goodsitem__wrap'>
        <div className='goodsitem__number '>
          <p className='mini'>{+page * 10 - 10 + index + 1}</p>
        </div>
        <div className='goodsitem__type typename'>
          <p className='mini'>{item?.type?.name}</p>
        </div>
        <div className='goodsitem__type brandname'>
          <p className='mini'>
            {item?.brand?.name.length <= 10
              ? item.brand.name
              : item.brand.name
                  .split('')
                  .filter((item, index) => index <= 9)
                  .join('')}
            {item?.brand?.name.length > 10 && (
              <span
                title={item.brand.name
                  .split('')
                  .filter((_, index) => index > 9)
                  .join('')}
              >
                ...
              </span>
            )}
          </p>
        </div>
        <div className='goodsitem__type goodsname'>
          <p className='mini'>
            {item.name.length <= 10
              ? item.name
              : item.name
                  .split('')
                  .filter((_, index) => index <= 9)
                  .join('')}
            {item.name.length > 10 && (
              <span
                title={item.name
                  .split('')
                  .filter((_, index) => index > 9)
                  .join('')}
              >
                ...
              </span>
            )}
          </p>
        </div>
        <div className='goodsitem__type pricename'>
          <p className='mini'>${item?.price}</p>
        </div>

        <div className='goodsitem__svg changetype'>
          <Link to={item?.change && links.ADMINGOODSADD + '/' + item.id}>
            <Pen />
          </Link>
        </div>
        <div
          onClick={item?.change ? deleteItem : undefined}
          className='goodsitem__svg deletetype'
        >
          <Basket />
        </div>
      </div>
    </div>
  );
};

type GoodsType = {
  item: FieldValues;
  index: number;
  fetching: () => void;
  page: string;
};
