import { useLocation, useNavigate } from 'react-router-dom';
import { Harmonic } from './Components/Harmonic/Harmonic';
import { UserBlock } from './Components/UserBlock/UserBlock';
import {
  adminArray,
  userArray,
  UserHarmonic,
} from './Components/UserHarmonic/UserHarmonic';
import { ReactComponent as Exit } from '../../img/icons/exit.svg';
import { useEffect, useContext } from 'react';
import { ChangePass } from './Components/ChangePass/ChangePass';
import { ReactComponent as Indent } from '../../img/icons/indent.svg';
import Menu from '../Shop/Tuner/components/Menu/Menu';
import { addClassMenu, showMenu } from '../../jsFunction';
import { exitUser, useAppDispatch, useAppSelector } from '../../store';
import { Setting } from '../Setting/Setting';
import { ContextPortal } from '../../components/UI/Portal/PortalCont';

import './User.scss';

const UserSelector: React.FC = () => {
  const router = useLocation().pathname;

  useEffect(() => {
    showMenu();
  }, []);

  return (
    <>
      <Menu name='usermenu' />
      <div className='user'>
        <div className='container'>
          <div className='user__wrap'>
            <UserAside />
            <div className='user__body'>
              {router.includes('user') ? <UserSection /> : <Setting />}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export const UserSection: React.FC = () => {
  const router = useLocation().pathname;

  useEffect(() => {
    document.title = `User`;
  });
  return <>{router.includes('pass') ? <ChangePass /> : <UserBlock />}</>;
};

export const UserAside: React.FC = () => {
  const { user } = useAppSelector((state) => state.user);
  const dispatch = useAppDispatch();
  const router = useLocation().pathname;
  const navigate = useNavigate();
  const { toggleAdd } = useContext(ContextPortal);

  const exit = () => {
    dispatch(exitUser());
    navigate('/');
    toggleAdd({
      en: 'Logged out successfully',
      ru: 'Вы успешно вышели из системы',
      time: 2000,
    });
  };

  return (
    <div className='useraside'>
      <div className='useraside__wrapper'>
        <div className='useraside__blockfirst'>
          <div className='useraside__setting'>
            <Harmonic submit={router.includes('user')} name='Account'>
              {userArray.map((item) => (
                <UserHarmonic key={item.name} item={item} />
              ))}
            </Harmonic>
            {'id' in user && user.role == 'Admin' && (
              <Harmonic submit={router.includes('admin')} name='Settings'>
                {adminArray.map((item) => (
                  <UserHarmonic key={item.name} item={item} />
                ))}
              </Harmonic>
            )}
          </div>
        </div>
        <div onClick={addClassMenu} className='useraside__blocksecond'>
          <div className='useraside__filter'>
            <Indent />
          </div>
        </div>

        <div className='useraside__exit exit'>
          <div onClick={exit} className='exit__wrap'>
            <div className='exit__text'>
              <p className=''>Exit</p>
            </div>
            <div className='exit__img'>
              <Exit />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default UserSelector;
