import { useContext, useEffect, useState } from 'react';
import { FieldValues, SubmitHandler, useForm } from 'react-hook-form';
import { useLocation, useNavigate, useParams } from 'react-router-dom';
import { Select, SelectFather } from './components/Select/Select';
import { selectedMine } from '../../../Shop/Tuner/components/selectedMine';
import './ChangeGoods.scss';
import { TextareItem } from './components/TextareItem/TextareItem';
import { Price } from './components/Price/Price';
import { ProductFileds } from './components/ProductFileds/ProductFileds';
import { useAppDispatch, useAppSelector } from '../../../../store';
import { $, BtnLoading, Loading, LoadingWin } from '../../../../components';
import { basketAll } from '../../../../store/goodsSlices/basketSlice';
import { favoriteAll } from '../../../../store/goodsSlices/favoriteSlice';
import {
  changeProductHTTP,
  deleteDeviceHTTP,
  getProductSelectHTTP,
  productTypesHTTP,
  setImgProductHTTP,
  setProductHTTP,
} from '../../../../store/goodsSlices/deviceListSlice/deviceListAPI';
import { changeOldFiled } from '../../../../store/goodsSlices/deviceListSlice/deviceListSlice';
import { ContextPortal } from '../../../../components/UI/Portal/PortalCont';
import useLoading from '../../../../components/UX/Hooks/useLoading';

export const ChangeGoods: React.FC = () => {
  const { toggleAdd } = useContext(ContextPortal);
  const dispatch = useAppDispatch();
  const { isLoading, product, brands, colors, types } = useAppSelector(
    (state) => state.deviceSelect
  );

  const location = useLocation().pathname;
  const path = useParams().id;
  const router = useNavigate();
  const [imgOld, setImgOld] = useState('');
  const [itemproduct, setItemProduct] = useState<FieldValues>({});
  const [files, setFiles] = useState<any>();

  const { register, setValue, handleSubmit, getValues } = useForm({
    mode: 'onSubmit',
  });

  async function fetchingGet() {
    dispatch(productTypesHTTP());

    if (path) {
      dispatch(getProductSelectHTTP({ id: +path }));
    } else {
      setValue('category', '');
      setValue('brand', '');
      setValue('color', '');
      setValue('price', '');
      setValue('sale', '');
      setValue('information', '');
      setValue('name', '');
      setImgOld('');

      dispatch(changeOldFiled({ old: false }));
    }
  }

  useEffect(() => {
    fetchingGet();
  }, [location]);

  useEffect(() => {
    if (path && !isLoading && 'type' in product) {
      setItemProduct(product);

      setValue('category', product.type.id);
      setValue('brand', product.brand.id);
      setValue('color', product.colorId);
      setValue('price', product.price);
      setValue('sale', product.sale);
      setValue('information', product.info);
      setValue('name', product.name);
      setImgOld(product.deviceimg.img);

      dispatch(changeOldFiled({ old: true }));
    }
  }, [product]);

  const handelrClick = (
    e: React.MouseEvent<HTMLElement>,
    item?: FieldValues
  ) => {
    e.preventDefault();
    const target = e.target as HTMLElement;
    const li = target.closest('li');
    const block = target.closest('.select') as HTMLElement;
    if (li?.textContent && li) {
      selectedMine(e, 'li');
    } else if (block) {
      $(block).toggle('add');
    }
  };

  const deleteItem = async () => {
    if (path && itemproduct?.change) {
      dispatch(deleteDeviceHTTP({ id: +path }));
      dispatch(favoriteAll());
      dispatch(basketAll());

      router('/admin/goods?page=1');
    }
  };

  const [isLoadingFetch, fetching] = useLoading(async function (data) {
    if (
      getValues('category') != '' &&
      getValues('brand') != '' &&
      getValues('color') != ''
    ) {
      let response;
      if (path) {
        response = await changeProductHTTP(data, +path, imgOld, files);
        if (response?.message == 'Ok') {
          toggleAdd({
            en: 'Product changed successfully',
            ru: 'Продукт успешно изменен',
            time: 5000,
          });
        }
      } else {
        const deviceimgId = await setImgProductHTTP(files);

        if (deviceimgId.message == 'Ok') {
          response = await setProductHTTP(data, deviceimgId.body.id);
          if (response.message == 'Ok') {
            toggleAdd({
              en: 'Product added successfully',
              ru: 'Продукт успешно добавлен',
              time: 5000,
            });
          }
        }
      }

      if (response.message == 'Ok') {
        router('/admin/goods?page=1');
      } else {
        toggleAdd({
          en: response.message,
          err: 'err',
          time: 5000,
        });
      }
    }
  });

  const submit: SubmitHandler<FieldValues> = function (data) {
    fetching(data);
  };

  return (
    <div className='changegoods'>
      <LoadingWin isLoading={isLoading}>
        <form onSubmit={handleSubmit(submit)} className='changegoods__form'>
          <div className='changegoods__name'>
            <div className='changegoods__nameblock'>
              <div className='titleproductadmin block '>
                <h3>Product Name</h3>
              </div>
              <div className='changegoods__nameinput'>
                <input
                  placeholder='Name'
                  {...register('name', { required: 'Field must be filled' })}
                />
              </div>
            </div>
          </div>
          <div className='changegoods__select'>
            <SelectFather name='Category'>
              <Select
                name='category'
                register={register}
                setValue={setValue}
                getValues={getValues}
                handelrClick={handelrClick}
                array={types}
              />
            </SelectFather>
            <SelectFather name='Brand'>
              <Select
                name='brand'
                getValues={getValues}
                register={register}
                setValue={setValue}
                handelrClick={handelrClick}
                array={brands}
              />
            </SelectFather>
            <SelectFather name='Color'>
              <Select
                name='color'
                getValues={getValues}
                register={register}
                setValue={setValue}
                handelrClick={handelrClick}
                array={colors}
              />
            </SelectFather>
          </div>
          <div className='changegoods__numbers'>
            <Price
              setValue={setValue}
              register={register}
              name='price'
              col={5}
            />
            <Price
              setValue={setValue}
              register={register}
              name='sale'
              col={2}
            />
          </div>

          <TextareItem register={register} name='information' />
          <div className='changegoods__imageblock'>
            <ProductFileds
              setFiles={setFiles}
              setImgOld={setImgOld}
              imgOld={imgOld}
            />
          </div>

          <div className='changegoods__footer'>
            {!!path && itemproduct?.change && (
              <button onClick={deleteItem} className='changegoods__delete'>
                <p>DELETE</p>
              </button>
            )}
            {isLoadingFetch ? (
              <button className='changegoods__btn'>
                <BtnLoading>
                  <Loading />
                </BtnLoading>
              </button>
            ) : !path ? (
              <button type='submit' className='changegoods__btn'>
                <p>SEND</p>
              </button>
            ) : (
              itemproduct?.change && (
                <button type='submit' className='changegoods__btn'>
                  <p>UPDATE</p>
                </button>
              )
            )}
          </div>
        </form>
      </LoadingWin>
    </div>
  );
};
