import { AddBlockTypes, SelectHeadTypes, SelectType } from './Select.conf';
import { useState, useContext, useEffect } from 'react';
import { ReactComponent as Basket } from '../../../../../../img/icons/trash.svg';

import './Select.scss';
import { $ } from '../../../../../../components';
import {
  deleteTypeSelectHTTP,
  setBORTSelectHTTP,
  setColorSelectHTTP,
} from '../../../../../../store/goodsSlices/deviceListSlice/deviceListAPI';
import { useAppDispatch, useAppSelector } from '../../../../../../store';

export const Select: React.FC<SelectType> = ({
  name,
  setValue,
  getValues,
  register,
  handelrClick,
  array,
}) => {
  const [add, setAdd] = useState(false);
  const [element, setElement] = useState(
    array?.filter((item) => item.id == getValues(name))
  );
  const dispatch = useAppDispatch();
  const { old } = useAppSelector((state) => state.deviceSelect);

  const handlerDelete = async (item) => {
    await dispatch(deleteTypeSelectHTTP({ title: name, id: item.id }));

    if (element.length == 1 && element[0].id == getValues(name)) {
      setValue(name, '0');
      setElement([]);
    }

    setAdd(false);
  };

  useEffect(() => {
    setElement(array?.filter((item) => item.id == getValues(name)));
  }, [old]);

  const handler = (e, item) => {
    setValue(name, getValues(name) == item.id ? 0 : item.id);
    setElement([item]);
    handelrClick(e, item);
  };

  const addDescription = () => {
    setAdd(true);
    setTimeout(() => {
      $('.select__inputtext input:first-child').focus();
    });
  };

  return (
    <div data-filter className='select'>
      <div onClick={(e) => handelrClick(e)} className='select__head'>
        <p>
          {element?.length && element.length == 1
            ? array?.filter((item) => item.id == element[0].id)[0].name
            : name.split('').map((item2: string, index2: number) => {
                return index2 == 0 ? item2.toUpperCase() : item2.toLowerCase();
              })}
        </p>
        <p className='select__arrow mini'>{'>'}</p>
      </div>
      <div className='select__blockharmonic'>
        <div className='select__select'>
          <select {...register(name)} />
        </div>

        <ul className='select__blockemersion'>
          {array?.map((item) => {
            return (
              <li
                className={item?.id == getValues(name) ? 'add' : ''}
                key={item.name}
              >
                <div
                  onClick={(e) => handler(e, item)}
                  className='select__check'
                >
                  <div className='select__ok'></div>
                  <div className='select__text'>
                    <p>
                      {item.name
                        .split('')
                        .map((item2: string, index2: number) => {
                          return index2 == 0
                            ? item2.toUpperCase()
                            : item2.toLowerCase();
                        })}
                    </p>
                  </div>
                  {name == 'color' && item.color != 'null' && (
                    <div
                      className='select__color'
                      style={{ background: `${item.color}` }}
                    ></div>
                  )}
                </div>
                <div
                  onClick={() => handlerDelete(item)}
                  className='select__delete'
                >
                  <Basket />
                </div>
              </li>
            );
          })}
          {add ? (
            <AddNewBlock title={name} setAdd={setAdd} />
          ) : (
            <li className='select__addnew' onClick={addDescription}>
              <div className='select__plus'>
                <p>+</p>
              </div>
              <div className='select__text'>
                <p>Add {name}</p>
              </div>
            </li>
          )}
        </ul>
      </div>
    </div>
  );
};

export const SelectFather: React.FC<SelectHeadTypes> = ({ name, children }) => {
  return (
    <div className='selecthead'>
      <div className='titleproductadmin block'>
        <h3>{name}</h3>
      </div>
      {children}
    </div>
  );
};

export const AddNewBlock: React.FC<AddBlockTypes> = ({ title, setAdd }) => {
  const [value, setValue] = useState('');
  const [valueColor, setValueColor] = useState('#ffffff');
  const dispatch = useAppDispatch();

  const handler = async () => {
    if (title == 'brand' || title == 'category') {
      await dispatch(setBORTSelectHTTP({ name: value, title }));
    } else if (title == 'color') {
      await dispatch(setColorSelectHTTP({ name: value, color: title }));
    }
    setAdd(false);
  };

  return (
    <div className='select__add'>
      <div className='select__inputtext'>
        <input
          onChange={(e) => setValue(e.target.value)}
          value={value}
          type='text'
          placeholder={`new ${title}`}
        />
      </div>
      {title == 'color' && (
        <div className='select__inputcolor'>
          <input
            value={valueColor}
            onChange={(e) => setValueColor(e.target.value)}
            type='color'
            name='color'
          />
        </div>
      )}
      <div onClick={handler} className='select__btn'>
        <p>Add</p>
        <p>{'>'}</p>
      </div>
    </div>
  );
};
